body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  height: 100%;
  width: 100%;
}

.content-container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.input-text-component-container {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.form-group-input-text {
  width: 80%;
  max-width: 500px;
}

.transcriptions-component-container {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.form-group-transcription {
  width: 80%;
  max-width: 500px;
}
